.attractor-renderer .event-target {
  fill: white;
  fill-opacity: 10e-6;
  cursor: pointer; }
  .attractor-renderer .event-target.selected {
    stroke: steelblue;
    stroke-dasharray: 1 1; }

.attractor-renderer.positive .background {
  fill: green; }

.attractor-renderer.negative .background {
  fill: red; }



